@import url(https://fonts.googleapis.com/css?family=Montserrat:400,700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

section {
	padding: 5rem 0rem;
}

#hero {
	min-height: 100vh;
	height: 100vh;
	display: flex;
	align-items: center;
	border-bottom: 0px;
	background: #ffffff;
	font-weight: 400;
	color: #272341;
	padding: 0rem 5.6rem;
	margin-bottom: 0;
	top: 0;
	left: 0;
	bottom: 0;
	right: 0;
	z-index: -1;
}

.hero-title {
	font-size: 5.6rem;
	font-weight: 700;
	margin-bottom: 3.2rem;
	text-align: left;
}

.hero-cta {
	text-align: left;
}

/* tab-land */
@media only screen and (max-width: 75em) {
	.hero-title {
		font-size: 4rem;
	}
}

/* tab-port */
@media only screen and (max-width: 56.25em) {
	.hero-title {
		font-size: 3.6rem;
		text-align: center;
	}
}

/* phone */
@media only screen and (max-width: 37.5em) {
	section {
		border: none;
		padding-left: 1rem;
		padding-right: 1rem;
	}

	#hero {
		padding: 0rem 1.6rem;
	}

	.hero-title {
		font-size: 3.5rem;
		line-height: 1.5;
	}
}

/* phone-xs */
@media only screen and (max-width: 20em) {
	.hero-title {
		font-size: 2.8rem;
	}
}

#about {
	background-color: #02aab0;
	background-image: linear-gradient(135deg, #02aab0 0%, #00cdac 100%);
	color: #ffffff;
	height: 100%;
	border-top: 0px;
	-webkit-clip-path: polygon(0 0, 100% 0, 100% 80%, 0 100%);
	clip-path: polygon(0 0, 100% 0, 100% 80%, 0 100%);
	padding-bottom: 10%;
}

.about-wrapper-info {
	display: flex;
	height: 100%;
	justify-content: center;
	flex-direction: column;
}

.about-wrapper-image {
	display: flex;
	height: 100%;
	align-items: center;
	justify-content: center;
}

.about-image {
	width: 60%;
}

.about-wrapper-info-text {
	text-align: left;
	margin-bottom: 1.6rem;
}

.about-wrapper-text-important {
	background: darken(#00cdac, 1%);
	display: inline-block;
	font-style: italic;
	padding: 0 0.3rem;
	margin: 0.3rem 0;
	line-height: 1.6;
}

.cta-btn {
	display: inline-block;
	position: relative;
	padding: 0.8rem 1.6rem;
	font-weight: bold;
	line-height: 1;
	z-index: 1;
	transition: all cubic-bezier(0.19, 1, 0.22, 1) 0.6s;
}

.cta-btn::after {
	content: '';
	display: block;
	position: absolute;
	width: 0px;
	height: 100%;
	left: 0;
	bottom: 0;
	z-index: -1;
	transition: all cubic-bezier(0.19, 1, 0.22, 1) 0.3s;
}

/* Hero Style */
.cta-btn--hero {
	background-image: linear-gradient(135deg, #02aab0 0%, #00cdac 100%);
	background-clip: text;
	-webkit-background-clip: text;
	-webkit-text-fill-color: transparent;
	border-width: 2px;
	border-style: solid;
	border-image: linear-gradient(135deg, #02aab0 0%, #00cdac 100%);
	border-image-slice: 1;
	cursor: pointer;
}

.cta-btn--hero::after {
	background-image: linear-gradient(135deg, #02aab0 0%, #00cdac 100%);
}

.cta-btn--hero:hover {
	-webkit-text-fill-color: #fff;
	text-decoration: none;
}

.cta-btn--hero:hover::after {
	width: 100%;
}

.cta-btn--resume {
	color: #fff;
	border: 2px solid #fff;
}

.cta-btn--resume::after {
	background: #fff;
}

.cta-btn--resume:hover {
	color: #00cdac;
	text-decoration: none;
}

.cta-btn--resume:hover::after {
	width: 100%;
}

/* Arrow Button */
.up i {
	color: #272727;
}

/* tab-land */
@media only screen and (max-width: 75em) {
	#about {
		height: 100%;
		-webkit-clip-path: none;
		clip-path: none;
	}

	.about-wrapper-image {
		padding-bottom: 6.4rem;
	}
}

/* tab-port */
@media only screen and (max-width: 56.25em) {
	.about-wrapper-image {
		height: 100%;
	}

	.about-wrapper-text {
		font-size: 1.5rem;
		text-align: center;
	}
}

/* phone */
@media only screen and (max-width: 37.5em) {
	.about-wrapper {
		padding-bottom: 5rem;
	}

	.about-wrapper-info {
		align-items: center;
	}

	.about-wrapper-text {
		font-size: 1.6rem;
		text-align: center;
	}

	.about-wrapper-text-important {
		display: inline;
		margin: 0;
		padding: 0;
		background: transparent;
		font-style: normal;
	}

	.cta-btn--hero {
		background-image: none;
		border: 2px solid #00cdac;
		-webkit-text-fill-color: #00cdac;
	}

	.cta-btn--hero::after {
		background-image: none;
	}

	.cta-btn--hero:hover {
		-webkit-text-fill-color: #00cdac;
	}
}

#projects {
