.footer {
	background-color: #333;
	color: #fff;
	padding: 4.8rem 0;
}

.footer-text {
	color: #808080;
	font-size: 1.3rem;
}

.footer-text a {
	color: #808080;
	font-size: 1.3rem;
	transition: all 0.4s;
	display: inline-block;
	background-color: #333;
}

.footer-text a:hover,
.footer-text a:active {
	color: #02aab0;
	box-shadow: 0 1.5rem 4rem rgba(0, 0, 0, 0.15);
}

.footer hr {
	margin: 1rem auto;
	border: 0;
	width: 50%;
	border-top: 2px solid rgba(255, 255, 255, 0.1);
}

.social-links {
	display: flex;
	flex-direction: row;
	justify-content: center;
}

.social-links a {
	display: flex;
	flex-direction: column;
	justify-content: center;
	color: #fff;
	font-size: 3rem;
	width: 5rem;
	height: 5rem;
	margin: 0.5rem 1.6rem;
	transition: all 0.2s ease-in;
}

.social-links a:hover {
	transform: translateY(-2px);
}

.back-to-top i {
	color: #fff;
	margin: 0.5rem 0 1.6rem;
	transition: all 0.2s ease-in;
	cursor: pointer;
}

.back-to-top i:hover {
	transform: translateY(-2px);
}

